<template>
  <div class="form form-find" v-loading="loading">
    <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')">
      <el-form-item label="" prop="email">
        <el-input v-model="form.email" placeholder=") man2sfix@man2sfix.com"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input type="text" v-model="form.phone" placeholder=") 010-0000-0000"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button native-type="submit" plain class="btn-block"> </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Validator from '@/mixins/validator/Validator'

export default {
  name: 'FindPassword',
  mixins: [
    Validator
  ],
  data () {
    return {
      loading: false,
      form: {
        email: '',
        phone: ''
      },
      rules: {
        email: [
          { required: true, message: ' .', trigger: 'blur' },
          { type: 'email', message: '  .', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '  .', trigger: 'blur' },
          { validator: this.validatePhone, message: '   .', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          //  
          this.loading = true
          //   
          const formData = {
            email: this.form.email,
            phone: this.form.phone
          }
          //  boolean
          const bool = await this.$store.dispatch('findPassword', formData)
          //   
          if (bool) {
            // todo
          }
          //  
          this.loading = false
        } else {
          return false
        }
      })
    }
  }
}
</script>



