<template>
  <div class="form" v-loading="loading">
    <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')">
      <el-form-item label="" prop="name">
        <el-input v-model="form.name" placeholder=") "></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="form.email" placeholder=") man2sfix@man2sfix.com"></el-input>
        <el-checkbox v-model="form.emailAgree"> </el-checkbox>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="form.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label=" " prop="passwordConfirm">
        <el-input type="password" v-model="form.passwordConfirm" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label=" " prop="phone">
        <div class="item-phone">
          <el-input type="text" v-model="form.phone" placeholder=") 010-0000-0000"></el-input>
        </div>
        <el-checkbox v-model="form.phoneAgree">SMS </el-checkbox>
      </el-form-item>
      <!--   -->
      <el-form-item label="" prop="gender">
        <el-radio-group v-model="form.gender">
          <el-radio label="man"></el-radio>
          <el-radio label="woman"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="birthday">
        <el-date-picker v-model="form.birthday" type="date" placeholder=") 0000-00-00"></el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="greeting">
        <el-input type="textarea" v-model="form.greeting" :autosize="{ minRows: 3, maxRows: 10}"></el-input>
      </el-form-item>
      <el-form-item label=" (, , )" prop="academicFile" class="relative-box">
        <el-upload action="#" :auto-upload="false" :limit="1" :on-change="uploadAcademicFile" :on-remove="removeAcademicFile">
          <el-button size="small" plain></el-button>
          <div slot="tip" class="el-upload__tip">2MB  gif/jpg/png/pdf  .</div>
        </el-upload>
      </el-form-item>
      <el-form-item label=" (, )" prop="careerFile" class="relative-box">
        <el-upload action="#" :auto-upload="false" :limit="1" :on-change="uploadCareerFile" :on-remove="removeCareerFile">
          <el-button size="small" plain></el-button>
          <div slot="tip" class="el-upload__tip">2MB  gif/jpg/png/pdf  .</div>
        </el-upload>
      </el-form-item>
      <el-form-item label=" (  )" prop="completionFile" class="relative-box">
        <el-upload action="#" :auto-upload="false" :limit="1" :on-change="uploadCompletionFile" :on-remove="removeCompletionFile">
          <el-button size="small" plain></el-button>
          <div slot="tip" class="el-upload__tip">2MB  gif/jpg/png/pdf  .</div>
        </el-upload>
      </el-form-item>
      <!-- //   -->
      <el-form-item label="" class="relative-box">
        <el-upload list-type="picture" action="#" :auto-upload="false" :limit="1" :on-change="uploadProfile" :on-remove="removeProfile">
          <el-button size="small" plain></el-button>
          <div slot="tip" class="el-upload__tip">1MB  gif/jpg/png  .</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="terms" class="relative-box">
        <el-checkbox v-model="form.terms"></el-checkbox>
        <el-button plain size="mini" @click="termsVisible = true"></el-button>
      </el-form-item>
      <el-form-item label="" prop="privacy" class="relative-box">
        <el-checkbox v-model="form.privacy"></el-checkbox>
        <el-button plain size="mini" @click="privacyVisible = true"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button native-type="submit" plain class="btn-block"></el-button>
      </el-form-item>
    </el-form>
    <el-dialog title="" :visible.sync="termsVisible" class="signup-dialog">
      <div class="signup-dialog-inner">
        <sign-up-terms></sign-up-terms>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="privacyVisible" class="signup-dialog">
      <div class="signup-dialog-inner">
        <sign-up-privacy></sign-up-privacy>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import SignUpStudent from '@/components/auth/SignUpStudent'
import SignUpTerms from '@/components/auth/SignUpTerms'
import SignUpPrivacy from '@/components/auth/SignUpPrivacy'

export default {
  name: 'SignUpInstructor',
  extends: SignUpStudent,
  data () {
    return {
      form: {
        name: '',
        email: '',
        password: '',
        passwordConfirm: '',
        phone: '',
        gender: null,
        birthday: '',
        greeting: '',
        academicFile: '',
        careerFile: '',
        completionFile: '',
        profile: '',
        terms: false,
        privacy: false,
        emailAgree: true,
        phoneAgree: true,
        instructorVerified: false,
        instructorActive: true
      },
      rules: {
        name: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 2, max: 5, message: ' 2  5  .', trigger: 'blur' }
        ],
        email: [
          { required: true, message: ' .', trigger: 'blur' },
          { type: 'email', message: '  .', trigger: 'blur' }
        ],
        password: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 6, message: ' 6  .', trigger: 'blur' },
          { validator: this.validatePassword, trigger: 'blur' }
        ],
        passwordConfirm: [
          { required: true, message: ' .', trigger: 'blur' },
          { validator: this.validatePasswordConfirm, trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '  .', trigger: 'blur' },
          { validator: this.validatePhone, message: '   .', trigger: 'blur' }
        ],
        gender: [
          { required: true, message: ' .', trigger: 'change' }
        ],
        birthday: [
          { required: true, message: ' .', trigger: 'blur' }
        ],
        greeting: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 100, message: '  100  .', trigger: 'blur' }
        ],
        academicFile: [
          { required: true, message: '  ', trigger: 'blur' }
        ],
        careerFile: [
          { required: true, message: '  .', trigger: 'blur' }
        ],
        completionFile: [
          { required: true, message: '  .', trigger: 'blur' }
        ],
        terms: [
          { required: true, message: ' .', trigger: 'change' },
          { validator: this.validateCheck, message: ' .', trigger: 'change' }
        ],
        privacy: [
          { required: true, message: ' .', trigger: 'change' },
          { validator: this.validateCheck, message: ' .', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    uploadFile (file, fileList, key) {
      const isSize = file.size <= 2 * 1024 * 1024
      const isType = file.raw.type === 'image/jpg' || file.raw.type === 'image/png' || file.raw.type === 'image/jpeg' || file.raw.type === 'image/gif' || file.raw.type === 'application/pdf'

      if (isSize && isType) {
        this.form[key] = file.raw
      } else {
        fileList.pop()
        this.$message({
          showClose: true,
          message: '    .',
          type: 'warning'
        })
      }
    },
    uploadAcademicFile (file, fileList) {
      this.uploadFile(file, fileList, 'academicFile')
    },
    removeAcademicFile (file, fileList) {
      this.form.academicFile = ''
    },
    uploadCareerFile (file, fileList) {
      this.uploadFile(file, fileList, 'careerFile')
    },
    removeCareerFile (file, fileList) {
      this.form.careerFile = ''
    },
    uploadCompletionFile (file, fileList) {
      this.uploadFile(file, fileList, 'completionFile')
    },
    removeCompletionFile (file, fileList) {
      this.form.completionFile = ''
    },
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          //  
          this.loading = true
          //   set
          const formData = new FormData()
          formData.append('type', 'instructor')
          formData.append('name', this.form.name)
          formData.append('email', this.form.email)
          formData.append('password', this.form.password)
          formData.append('phone', this.form.phone)
          formData.append('gender', this.form.gender)
          formData.append('birthday', this.form.birthday)
          formData.append('greeting', this.form.greeting)
          formData.append('academicFile', this.form.academicFile)
          formData.append('careerFile', this.form.careerFile)
          formData.append('completionFile', this.form.completionFile)
          formData.append('instructorVerified', this.form.instructorVerified)
          formData.append('instructorActive', this.form.instructorActive)
          formData.append('profile', this.form.profile)
          formData.append('terms', this.form.terms)
          formData.append('privacy', this.form.privacy)
          formData.append('emailAgree', this.form.emailAgree)
          formData.append('phoneAgree', this.form.phoneAgree)
          //  boolean
          const bool = await this.$store.dispatch('signUp', formData)
          //   
          if (bool) {
            this.$message({
              showClose: true,
              message: ' .',
              type: 'success'
            })
            this.$router.replace('/signin')
          }
          //  
          this.loading = false
        } else {
          return false
        }
      })
    }
  },
  components: {
    SignUpTerms,
    SignUpPrivacy
  }
}
</script>



